  display: none;
}

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.centered-content {
  max-width: 800px;
}

.align-right {
  display: flex;
  justify-content: end;
}

.button-yellow-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border-radius: 0.5rem;
  border: none;
  cursor: pointer;
}
.button-yellow-block:hover {
  background-color: #c77808;
}

.button-yellow {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border-radius: 0.5rem;
  border: none;
  cursor: pointer;
}
.button-yellow:hover {
  background-color: #c77808;
}

.button-green {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border-radius: 0.5rem;
  border: none;
  cursor: pointer;
  margin: 0 0 2rem 0;
}
.button-green:hover {
  background-color: #619800;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .ad {
  background-color: #333333;
  border: none;
}
.dark-mode .ad .price {
  color: #71B100;
}
.dark-mode .characteristics-icon img {
  filter: invert(100%);
}
.dark-mode .form p, .dark-mode .form label, .dark-mode .form legend {
  color: #FFFFFF;
}
.dark-mode .form input:not([type=submit]), .dark-mode .form select, .dark-mode .form textarea {
  background-color: #333333;
  color: #FFFFFF;
  border: none;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 100vh;
  }
}
@media (min-width: 768px) {
  .header.inicio .header-content {
    min-height: 100vh;
  }
}

.header-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header-content h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: bold;
  max-width: 50rem;
  line-height: 2;
  margin-bottom: 8rem;
}

.footer {
  padding: 1rem 0;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .footer-content {
  padding-top: 2rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}
.footer .nav {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.derecha {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
    row-gap: 0;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  filter: invert(100%);
  cursor: pointer;
}

.bar {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  align-items: center;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
  }
}

.nav {
  height: 0;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s, opacity 0.3s height 0.5s;
}
@media (min-width: 768px) {
  .nav {
    display: block;
    visibility: visible;
    opacity: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    height: auto;
  }
}
.nav a {
  display: block;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
}
.nav a:hover {
  color: #71B100;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .icons-aboutUs {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.icon img {
  height: 10rem;
  margin: 0 auto;
}
.icon h3 {
  text-transform: uppercase;
}
.icon p {
  text-align: center;
}

@media (min-width: 768px) {
  .ads-content {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.ad {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  margin-bottom: 3rem;
}
.ad .ad-content {
  padding: 2rem;
}
.ad h3, .ad p {
  margin: 0;
}

.price {
  color: #71B100;
  font-weight: bold;
  font-size: 3rem;
  margin: 1rem 0 1rem 0;
}

.characteristics-icon {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 30rem;
}
.characteristics-icon li {
  display: flex;
  column-gap: 1rem;
}
.characteristics-icon li p {
  font-weight: bold;
}

.no-webp .contact-image {
  background-image: url("../img/encuentra.jpg");
}

.webp .contact-image {
  background-image: url("../img/encuentra.webp");
}

.contact-image {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contact-image h2 {
  font-size: 4rem;
  font-weight: bold;
  color: #FFFFFF;
}
.contact-image p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .section-bottom {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}

.entry-blog {
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .entry-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entry-blog img {
  border-radius: 0.5rem;
  box-shadow: 4px 4px 16px 0px rgba(0, 0, 0, 0.75);
}
.entry-blog:last-of-type {
  margin-bottom: 0;
}

.info-meta span {
  color: #E08709;
}

.entry-text a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .entry-text a {
    margin-top: 0;
  }
}
.entry-text h4 {
  margin: 0;
  line-height: 1.4;
}
.entry-text h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}

.testimonial {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial p {
  color: #FFFFFF;
  text-align: right;
  margin-right: 2rem;
  font-weight: bold;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  position: absolute;
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  left: -1rem;
  top: -2rem;
}

@media (min-width: 768px) {
  .aboutUs {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.aboutUs img {
  border-radius: 2rem;
  box-shadow: 4px 4px 16px 0px rgba(0, 0, 0, 0.75);
  margin-bottom: 3rem;
}
.aboutUs h4 {
  margin: 0;
}

.form {
  border-radius: 1em;
  padding: 2rem;
  margin-bottom: 5rem;
  box-shadow: 2px 5px 16px 2px rgba(0, 0, 0, 0.25);
}
.form p {
  font-size: 1.4rem;
  margin: 0 0 1rem 0;
  color: #333333;
}
.form p.error {
  color: #E08709;
  margin: 0;
}
.form legend {
  display: block;
  font-size: 2.8rem;
  font-weight: normal;
}
.form label {
  display: block;
  text-transform: uppercase;
  font-weight: bold;
}
.form input:not([type=submit]), .form select, .form textarea {
  display: block;
  padding: 1rem 2rem;
  border-radius: 1rem;
  width: 100%;
  border: 1px solid #e1e1e1;
  margin: 0.5rem 0;
}
.form input:not([type=submit]):focus, .form select:focus, .form textarea:focus {
  border: 1px solid #E08709;
}
.form textarea {
  height: 10rem;
  resize: none;
}
.form .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form .forma-contacto input {
  margin: 0;
  width: auto;
}

.submit {
  display: flex;
  justify-content: end;
  margin: 0 1.5rem 1rem 0;
}